<template>
    <div class="docker">
        <el-button
            v-for="(item,index) in buttonList"
            :key="item.id"
            type="primary"
            @click="showDetail(item, index)"
            size="small"
            class="margin-top-20">
            {{item.name}}
        </el-button>
    </div>
</template>

<script>

    export default {
        name: "FamilyTreeController",
        data() {
            return {}
        },
        methods: {
            showDetail(item, index) {
                console.log(index);
                let baseID = item.id == 0 ? 7 : item.id;
                let detailID = item.id == 0 ? 8 : item.taId;
                this.$store.commit({
                    type: 'familyTree/set_baseId',
                    baseID: baseID,
                    detailID: detailID,
                    editIndex: index
                });
            }
        },
        computed: {
            buttonList() {
                return this.$store.state.familyTree.buttonList;
            }
        }
    }
</script>

<style lang="less" scoped>
    .docker {
        position: relative;
        width: 100%;
        height: 100%;

        background: lightgreen;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: flex-start;
        align-content: flex-start;

        .el-button {
            margin-top: 10px;
            margin-left: 10px;
        }
    }
</style>



