<!--
需求分析：
Base：显示Base的父母兄妹子女；
Detail：显示Base的TA的父母兄妹子女；
-->
<template>
    <el-container>
        <el-main>
            <div class="main-part">
                <family-tree-base/>
            </div>
            <div class="main-part margin-top-1">
                <family-tree-detail/>
            </div>
            <div class="main-part margin-top-1">
                <family-tree-edit/>
            </div>
        </el-main>
        <el-aside width="260px">
            <family-tree-controller/>
        </el-aside>
    </el-container>
</template>

<script>
    import FamilyTreeBase from '../components/FamilyTree/FamilyTreeBase';
    import FamilyTreeController from '../components/FamilyTree/FamilyTreeController';
    import FamilyTreeDetail from '../components/FamilyTree/FamilyTreeDetail';
    import FamilyTreeEdit from "../components/FamilyTree/FamilyTreeEdit";

    export default {
        name: "FamilyTree",
        components: {
            FamilyTreeEdit,
            FamilyTreeDetail,
            FamilyTreeController,
            FamilyTreeBase
        }
    }
</script>

<style lang="less" scoped>
    .el-main {
        background: lightcyan;

        .main-part {
            position: relative;
            width: 100%;
            height: 300px;
        }

        .margin-top-1 {
            margin-top: 1%;
        }
    }

    .el-aside {
        background: lightblue;
        padding: 20px;
    }
</style>



