<template>
    <div class="docker-full">
        <div class="banner-left-mask">
        </div>
        <div class="banner-right-mask">
        </div>
        <p class="iknow-daily-date">
            {{today}}
        </p>
        <p class="banner-daily-info">
            <span class="banner-title">
            贸易战：为啥我国偏偏选了给美国大豆加关税？
            </span>
            <span class="banner-author">
            来自：图解金融
            </span>
        </p>
        <div class="iknow-daily-collection">
            <ul class="daily-colletion-list">
                <li class="daily-colletion-item">
                    <a class="colletion-item-link" target="_blank">
                        <img src="/static/img/main/wodaqing.png"
                             class="colletion-item-pic">
                    </a>
                    <span class="item-title-layout">
                        <span class="colletion-item-title">
                            <a class="text-value" target="_blank">为什么清朝的疆域这么大？</a>
                        </span>
                    </span>
                </li>
                <li class="daily-colletion-item">
                    <a class="colletion-item-link" target="_blank">
                        <img src="/static/img/main/ditie.png"
                             class="colletion-item-pic">
                    </a>
                    <span class="item-title-layout">
                        <span class="colletion-item-title">
                        <a class="text-value" target="_blank">通勤，正在"杀死"1000万北京青年</a>
                        </span>
                    </span>
                </li>
                <li class="daily-colletion-item">
                    <a class="colletion-item-link" target="_blank">
                        <img src="/static/img/main/dapangxie.png"
                             class="colletion-item-pic">
                    </a>
                    <span class="item-title-layout">
                        <span class="colletion-item-title">
                        <a class="text-value" target="_blank">就问一句：大闸蟹为啥那么香？</a>
                        </span>
                    </span>
                </li>
                <li class="daily-colletion-item">
                    <a class="colletion-item-link" target="_blank">
                        <img src="/static/img/main/meiguan.jpg"
                             class="colletion-item-pic">
                    </a>
                    <span class="item-title-layout">
                        <span class="colletion-item-title">
                        <a class="text-value" target="_blank">Misaqi, <br>follow me.</a>
                        </span>
                    </span>
                </li>
            </ul>
            <a class="more-daily" target="_blank" title="进入日报首页"/>
        </div>
    </div>
</template>

<script>
    import {getTimestamp} from '../../assets/js/DateUtil.js'

    export default {
        name: "BannerScroll",
        data() {
            return {
                today: getTimestamp('年月日', null)
            }
        }
    }
</script>

<style lang="less" scoped>
    .docker-full {
        background: url("/static/img/main/xiaoshengke.jpg") no-repeat;
        background-size: 100% 100%;

        .banner-left-mask {
            position: absolute;
            top: 0;
            left: 0;
            width: 150px;
            height: 400px;

            background: linear-gradient(to right, rgba(72, 74, 224, 1) 0%, rgba(0, 0, 0, 0) 100%);
        }

        .banner-right-mask {
            position: absolute;
            top: 0;
            right: 0;
            width: 150px;
            height: 400px;

            background: linear-gradient(to right, rgba(0, 0, 0, 0) 0%, rgba(72, 74, 224, 1) 100%);
        }

        .iknow-daily-date {
            color: #fff;
            font-size: 18px;
            line-height: 20px;
            position: absolute;
            top: 20px;
            left: 20px;
            text-shadow: 1px 1px 1px rgba(11, 2, 5, .65);
        }

        .banner-daily-info {
            position: absolute;
            top: 220px;
            left: 0;
            background: rgba(0, 0, 0, 0.3);
            padding: 6px 8px;
            cursor: pointer;
            text-align: left;

            .banner-title {
                white-space: nowrap;
                display: block;
                color: #fff;
                font-size: 30px;
                line-height: 32px;
                text-shadow: 0 0 2px #0b0205;
                margin-bottom: 6px;
            }
            .banner-author {
                white-space: nowrap;
                display: block;
                color: #fff;
                font-size: 12px;
                line-height: 14px;
                opacity: 0.7;
            }
        }

        .iknow-daily-collection {
            height: 83px;
            position: absolute;
            left: 0;
            bottom: 20px;

            .daily-colletion-list {
                display: inline;
                float: left;
                cursor: pointer;

                .daily-colletion-item {
                    display: inline;
                    float: left;
                    padding: 7px;
                    height: 69px;
                    width: 220px;
                    margin-right: 6px;
                    background: rgba(255, 255, 255, 0.91);

                    .colletion-item-link {
                        display: inline;
                        float: left;
                        width: 69px;
                        outline: 0;

                        .colletion-item-pic {
                            width: 69px;
                            height: 69px;
                            display: inline;
                            float: left;
                        }
                    }
                    .item-title-layout {
                        display: inline;
                        float: left;
                        width: 150px;
                        height: 69px;

                        .colletion-item-title {
                            font-size: 16px;
                            width: 136px;
                            padding-left: 14px;
                            height: 69px;
                            display: table-cell;
                            vertical-align: middle;

                            .text-value {
                                display: inline-block;
                                font-size: 16px;
                                line-height: 24px;
                                vertical-align: middle;
                                outline: 0;
                                text-decoration: none;
                            }
                        }
                    }
                }
                .daily-colletion-item:hover {
                    color: #35b558;
                }
            }

            .more-daily {
                display: inline;
                float: left;
                width: 36px;
                height: 36px;
                margin: 24px 12px;
                cursor: pointer;
                background: url("/static/img/main/module_bg.png") -410px -8px  no-repeat;
            }
            .more-daily:hover {
                background: url("/static/img/main/module_bg.png") -410px -47px  no-repeat;
            }
        }
    }
</style>



