function parseTreeData(treeData) {
    let buttonList = [];
    let baseItem = {
        id: '0',
        taId: '0',
        name: '我家',
        parentId: '0',
        description: ''
    };
    buttonList.push(baseItem);
    parseChildren(buttonList, treeData);
    return buttonList;
};

function parseChildren(list, children) {
    for (let i = 0; i < children.length; i++) {
        let item = children[i];
        if (item.description === 'root') {
            // console.log('root');
        } else {
            let button = {
                name: item.name,
                parentId: item.parentId,
                id: item.id,
                taId: item.taId,
                description: item.description
            };
            list.push(button);
        }
        if (item.children) {
            parseChildren(list, item.children);
        }
    }
    return list;
};

export {parseTreeData};



