<template>
    <el-container class="docker-main">
        <el-main>
            <div class="main-part docker-baner">
                <banner-scroll/>
            </div>
            <div class="main-part special-height mg-tp-1">
                <div class="docker-special-column">
                    <special-column/>
                </div>
                <div class="docker-bulletin-board">
                    <bulletin-board/>
                </div>
            </div>
            <div class="main-part mg-tp-1">
                <div class="docker-nav-pic">
                    <nav-book-box/>
                </div>
                <div class="docker-book-desc">
                    <router-view name="book-desc"/>
                </div>
            </div>
        </el-main>
        <el-aside width="310px" v-if="showAside">
            <sidebar-zhidao/>
            <sidebar-math class="margin-top-10"/>
            <sidebar-rank class="margin-top-10"/>
            <star-people class="margin-top-10"/>
        </el-aside>
    </el-container>
</template>

<script>
    import NavPicBox from './main/NavPicBox'
    import NavPicDesc from './main/NavPicDesc'
    import NavBookBox from './main/NavBookBox'
    import NavBookDesc from './main/NavBookDesc'
    import BannerScroll from './main/BannerScroll'
    import SpecialColumn from './main/SpecialColumn'
    import BulletinBoard from './main/BulletinBoard'
    import SidebarZhidao from './main/SidebarZhidao'
    import SidebarMath from './main/SidebarMath'
    import SidebarRank from './main/SidebarRank'
    import StarPeople from './main/StarPeople'

    export default {
        name: 'HelloWorld',
        components: {
            NavPicBox,
            NavPicDesc,
            NavBookBox,
            NavBookDesc,
            BannerScroll,
            SpecialColumn,
            BulletinBoard,
            SidebarZhidao,
            SidebarMath,
            SidebarRank,
            StarPeople
        },
        data() {
            return {
                fullWidth: document.documentElement.clientWidth
            }
        },
        mounted() {
            const that = this
            //监听window.onresize事件
            window.onresize = () => {
                return (() => {
                    window.fullWidth = document.documentElement.clientWidth

                    //0.5秒内不重复触发
                    if (!that.resizing) {
                        that.resizing = true
                        setTimeout(function () {
                            that.resizing = false
                            that.fullWidth = window.fullWidth
                        }, 1000)
                    }
                })()
            }
        },
        computed: {
            showAside() {
                if (this.fullWidth < 1520) {
                    return false;
                } else {
                    return true;
                }
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
    @el-main-width: 1064px;
    @main-part-width: @el-main-width - 40px;
    @main-part-height: 600px;

    .docker-main {
        justify-content: center;
    }

    .el-main {
        width: @el-main-width;
        color: #333;
        text-align: center;
        background-color: lightseagreen;
        flex: none;

        .main-part {
            position: relative;
            width: @main-part-width;
            height: @main-part-height;
            overflow: hidden;
            display: flex;

            .docker-special-column {
                width: 614px;
                height: 100%;

                background: darkkhaki;
            }
            .docker-bulletin-board {
                width: 400px;
                height: 100%;
                margin-left: 10px;
            }
            .docker-nav-pic {
                width: 410px;
                height: 100%;

                background: lightgrey;
            }
            .docker-book-desc {
                width: 604px;
                height: 100%;
                margin-left: 10px;
            }
        }
        .docker-baner {
            height: 400px;
            background: lightcyan;
        }

        .mg-tp-1 {
            margin-top: 1%;
        }

        .special-height {
            height: 420px;
        }
    }

    .el-aside {
        padding: 20px;
        text-align: center;
        background: lightgoldenrodyellow;

        .margin-top-10 {
            margin-top: 10px;
        }
    }
</style>



