// [indexDB](https://blog.csdn.net/Jensen_Yao/article/details/81737174)

const isIOS = !!navigator.userAgent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);//判断是否是ios
//console.log(navigator.userAgent.toLowerCase());
const isoVersion = 10.0;
if (isIOS) {
    const isoVersion = navigator.userAgent.match(/(iPhone\sOS)\s([\d_]+)/)[2].replace(/_/g, '.');//取出ios版本号
}
let DB = {
    dbName: "cmpDB",
    slqVersion: (isIOS && isoVersion.split(".")[0] > 9) ? "1.0" : "1",//websql的版本号，由于ios的问题，版本号的写法不一样
    dbVersion: 1,//indexDB的版本号
    storeName: "cmpStore",//store----即“表”的名字
    indexedDB: window.indexedDB || window.webkitIndexedDB, //监听IndexDB
    db: {}, //缓存数据库，避免同一个页面重复创建和销毁
    store: null,
    errorCode: {   //错误码
        open: 91001, //打开数据库失败的错误
        save: 91002, //保存数据失败的错误
        get: 91003,  //获取数据失败的错误
        delete: 91004, //删除数据失败的错误
        deleteAll: 91005 //清空数据库失败的错误
    },
    //打开数据库，并升级版本号
    init: function (callback, dbName) {
        //打开数据库
        if (DB.indexedDB) {
            console.log("=====init indexDb=====");
            let request = DB.indexedDB.open(dbName);
            request.onerror = function (e) {
                callback({code: DB.errorCode.open, error: e});
            };
            request.onsuccess = function (e) {
                console.log("=====open indexDb=====")
                DB.dbVersion = e.target.result.version;

                //更新的数据库
                let newRequest = DB.indexedDB.open(dbName, DB.dbVersion + 1);
                newRequest.onerror = function (e) {
                    console.error(e)
                };
                //onupgradeneeded 是我们唯一可以修改数据库结构的地方
                newRequest.onupgradeneeded = function (e) {
                    console.log("=====onupgradeneeded=====")
                    let newIndexDB = e.target.result;
                    //缓存数据库
                    DB.db[dbName] = newIndexDB;
                    //createObjectStore
                    //newIndexDB.deleteObjectStore('event-list');
                    //newIndexDB.createObjectStore('event_list', {keyPath: "key"});

                    callback("init sccess")
                }
                newRequest.onsuccess = function (e) {
                    console.log("====================")
                }
            }
        } else {
            console.log("=====indexDB unavailable=====")
        }
    },
    //打开数据库，并放入缓存中
    open: function (callback, dbName, storeName) {
        //打开数据库
        if (DB.indexedDB) {
            //如果支持IndexDB
            if (!DB.db[dbName]) {
                console.log("=====noCache indexDb=====");
                //如果缓存中没有，则进行数据库的创建或打开，提高效率
                let request = DB.indexedDB.open(dbName);
                request.onerror = function (e) {
                    callback({code: DB.errorCode.open, error: e});
                };
                request.onsuccess = function (e) {
                    console.log("=====open indexDb=====")
                    if (!DB.db[dbName]) {
                        DB.db[dbName] = e.target.result;
                    }
                    DB.dbVersion = e.target.result.version;
                    let store = DB.db[dbName].transaction(storeName, "readwrite").objectStore(storeName);
                    callback(store);
                };
            } else {
                //如果缓存中已经打开了数据库，就直接使用
                console.log("=====withCache indexDb=====");
                let store = DB.db[dbName].transaction(storeName, "readwrite").objectStore(storeName);
                callback(store);
            }
        } else {
            console.log("=====indexDB unavailable=====")
        }
    },
    save: function (dbName, storeName, key, value, callback) {//保存数据到数据库  key---数据key  value----数据值  callback----回调函数
        if (DB.indexedDB) {
            let inData = {
                key: key,
                value: value
            };
            DB.open(function (result) {
                let error = result.hasOwnProperty("error");
                if (error) {
                    callback && callback(result);
                } else {
                    let request = result.put(inData);
                    request.onsuccess = function (e) {
                        callback && callback({code: 'success'});
                    };
                    request.onerror = function (e) {
                        callback && callback({code: DB.errorCode.save, error: e});
                    }
                }
            }, dbName, storeName);
        } else {
            console.log("=====indexDB unavailable=====")
        }
    },
    get: function (dbName, storeName, key, callback) {//根据key获取值
        if (DB.indexedDB) {
            DB.open(function (result) {
                let error = result.hasOwnProperty("error");//判断返回的数据中是否有error字段
                if (error) {
                    callback(result);
                } else {
                    let request = result.get(key);
                    request.onsuccess = function (e) {
                        let data = e.target.result ? e.target.result.value : undefined;
                        callback({code: 'success', data: data});
                    };
                    request.onerror = function (e) {
                        callback({code: DB.errorCode.get, error: e});
                    }
                }
            }, dbName, storeName);
        } else {
            console.log("=====indexDB unavailable=====")
        }
    },
    delete: function (dbName, storeName, key, callback) {  //根据key删除某条数据
        if (DB.indexedDB) {
            DB.open(function (result) {
                let error = result.hasOwnProperty("error");
                if (error) {
                    callback && callback(result);
                } else {
                    let request = result.get(key);
                    request.onsuccess = function (e) {
                        let recode = e.target.result;
                        if (recode) {
                            request = result.delete(key);
                        }
                        callback && callback({code: 'success'});
                    };
                    request.onerror = function (e) {
                        callback && callback({code: DB.errorCode.delete, error: e});
                    };
                }
            }, dbName, storeName);
        } else {
            console.log("=====indexDB unavailable=====")
        }
    },
    deleteAll: function (dbName, storeName, callback) {//清空数据库
        if (DB.indexedDB) {
            DB.open(function (result) {
                let error = result.hasOwnProperty("error");
                if (error) {
                    callback && callback(result);
                } else {
                    result.clear();
                    callback && callback({code: 'success'});
                }
            }, dbName, storeName);
        } else {
            console.log("=====indexDB unavailable=====")
        }
    },
    _websqldelete: function (key, callback, dbName) { //私有方法，用于websql的删除
        DB.open(function () {
            DB.db[dbName].transaction(function (ts) {
                ts.executeSql("delete from " + DB.storeName + " where key = ? ", [key], function (ts, result) {
                    callback && callback({success: true});
                }, function (ts, e) {
                    callback && callback({code: DB.errorCode.delete, error: e});
                });
            });
        }, dbName);
    }
};

export default DB;



