import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Test from '../demos/TableView'
import store from '../store/index.js'
import LoginPage from '../pages/login/LoginPage.vue'
import MyPage from '../pages/personalCenter/MyPage.vue'
import EventList from '../pages/EventList'
import ThinkPad from '../pages/ThinkPad'
import FamilyTree from '../pages/FamilyTree'
import AboutMe from '../pages/AboutMe'
import DavinciDesc from '../components/main/DavinciDesc'
import NavBookDesc from '../components/main/NavBookDesc'

Vue.use(Router)

const router = new Router({
    routes: [
        {
            path: '/',
            name: 'HelloWorld',
            component: HelloWorld,
            children: [                                 //嵌套路由
                {
                    path: '',                           //命名路由，默认渲染
                    name: 'default',
                    components: {
                        'book-desc': DavinciDesc        //命名视图，没有命名则为default
                    }
                },
                // 当 /user/:id/profile 匹配成功，
                // UserProfile 会被渲染在 user 的 <router-view> 中
                {
                    path: 'book-info/:id',
                    name: 'book-info',
                    components: {
                        'book-desc': NavBookDesc
                    }
                }
            ]
        }, {
            path: '/login',
            name: 'login',
            component: LoginPage
        }, {
            path: '/user/:id',
            name: 'user',
            component: MyPage,
            meta: {
                requireAuth: true,  // 添加该字段，表示进入这个路由是需要登录的
            }
        }, {
            path: '/event-list',
            name: 'event-list',
            component: EventList
        }, {
            path: '/think-pad',
            name: 'think-pad',
            component: ThinkPad
        }, {
            path: '/family-tree',
            name: 'family-tree',
            component: FamilyTree
        }, {
            path: '/about-me',
            name: 'about-me',
            component: AboutMe
        }, {
            path: '/test/:debugMode',
            name: 'test',
            component: Test
        }, {
            path: '*',              //匹配不到，就是它了
            redirect: '/index.html'
        }
    ]
})

//注册全局钩子用来拦截导航
router.beforeEach((to, from, next) => {
    //获取store里面的token
    let token = store.state.token;
    //判断要去的路由有没有requiresAuth
    if (to.meta.requiresAuth) {
        if (token) {
            next();
        } else {
            next({
                path: '/login',
                query: {redirect: to.fullPath}  // 将刚刚要去的路由path（却无权限）作为参数，方便登录成功后直接跳转到该路由
            });
        }
    } else {
        next();//如果无需token,那么随它去吧
    }
});

export default router;



