const firstItem = {
    src: "/static/img/main/Davinci.jpg",
    title: 'Da Vinci'
};
const secondItem = {
    desc: "        上帝创造一切，但又把一切的秘密隐藏在生化密码之中。解开人类乃至万物的生化密码，成了达芬奇最感兴趣的事。他称此为“解构上帝”。通过解剖，这位科学怪人深刻地了解了血液和经络的奥秘。并且，通过对血液动力的研究，他甚至发现了超越人体极限的秘密。而这在宗教裁判所所营造的舆论下，被异化成了一种恐怖的黑暗科学。 “掌握生物密码的人就掌握了上帝”——达芬奇坚信着，对于世人的误解，他不以为意。但为了不激化矛盾，他选择了隐居。"
};
const thirdItem = {
    desc: "        利用在文艺复兴中经营起来的财富，达芬奇创立了黑暗科学的地下室，在那里，有关血液动力与机械发明的研究昼夜不息。在研究的过程中，达芬奇常有意外之喜。黑暗向这位充满渴望的探索者展示了世界美妙的另一面。 达芬奇创造了可以穿越光明与黑暗界限的飞行器，通过这架飞行器，他深入世界的尽头探索，在那里他发现了被历史与尘埃所遗忘的生物——魔鬼。一种被称为英魂的力量操控着它们——在后人找到的达芬奇的遗稿上，人们解读到了这段记述。而我们这位黑暗科学的创始者，最终也被黑暗本身所吞噬。"
};
const fourthItem = {
    src: "/static/img/main/huaxiangji.jpg",
    title: '滑翔机'
};
const fourGridData = {
    firstItem: firstItem,
    secondItem: secondItem,
    thirdItem: thirdItem,
    fourthItem: fourthItem
}
export default fourGridData;



