[图片放大缩小](https://segmentfault.com/q/1010000012038536)
[ElementUI](https://www.jb51.net/article/142638.htm)
<template>
    <div class="docker-nav-box">
        <div class="pic_div"
             v-for="(img,index) in navList" :key="index">
            <img class="img"
                 :src="img.src"
                 :title="img.title"
                 @click="showDetail(img.id)">
        </div>
    </div>
</template>

<script>
    import DemoData from '../../assets/demoData/main/NavBookBoxData'

    export default {
        name: "NavPicBox",
        data() {
            return {
                navList: DemoData.defaultInfo
            }
        },
        methods: {
            showDetail(id) {
                this.$router.push({name: 'book-info', params: {id: id}})
            }
        }
    }
</script>

<!--图片自适应-->
<!--1.只需通过css设置background-size属性为contain，即-->
<!--background-size:contain-->
<!--2.如下-->
<!--https://blog.csdn.net/zh_rey/article/details/69666232-->
<style lang="less" scoped>
    .docker-nav-box {
        position: relative;
        width: 100%;
        height: 100%;

        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
        align-content: flex-start;

        .pic_div {
            width: 132px;
            height: 90px;
            margin: 2px 2px;
            overflow: hidden;

            transform: scale(1);
            transition: all ease 0.2s;

            .img {
                position: relative;
                width: 100%;
            }
        }
        .pic_div:hover {
            transform: scale(1.5);
            z-index: 10;
        }
    }
</style>



