<template>
    <el-form ref="form"
             :model="editItem"
             :inline="true"
             label-width="100px"
             class="form-flex">
        <el-form-item label="NAME" class="my-family-tree-item">
            <el-input v-model="editItem.name" size="mini"></el-input>
        </el-form-item>
        <el-form-item label="PARENT-ID" class="my-family-tree-item">
            <el-input v-model="editItem.parentId" size="mini"></el-input>
        </el-form-item>
        <el-form-item label="ID" class="my-family-tree-item">
            <el-input v-model="editItem.id" size="mini" class="my-input"></el-input>
        </el-form-item>
        <el-form-item label="TA-ID" class="my-family-tree-item">
            <el-input v-model="editItem.taId" size="mini"></el-input>
        </el-form-item>
        <el-form-item label="描述" class="my-family-tree-item">
            <el-input type="textarea" v-model="editItem.description"></el-input>
        </el-form-item>
        <el-form-item class="margin-left">
            <el-button type="primary" @click="onSubmit" size="mini">立即创建</el-button>
            <el-button type="info" size="mini">取消</el-button>
        </el-form-item>
    </el-form>
</template>

<script>
    import apis from '../../apis/FamilyTree'

    export default {
        name: "FamilyTreeEdit",
        data() {
            return {}
        },
        methods: {
            onSubmit() {
                console.log(this.editItem);
                let sendData = {
                    formData: this.editItem
                };
                //调用api接口
                apis.updateData(sendData).then(res => {
                    let result = res.data.result;
                    console.log(result);
                    if (result == 'success') {
                        this.$message.success(result);
                    } else {
                        this.$message.error(result);
                    }
                }).catch(error => {
                    console.log(error);
                    this.$message.error('Operate Error')
                });
            }
        },
        computed: {
            editItem() {
                let index = this.$store.state.familyTree.editIndex;
                let buttonList = this.$store.state.familyTree.buttonList;
                return buttonList[index];
            }
        }
    }
</script>

<style lang="less">
    .my-family-tree-item {
        width: 45%;

        .el-form-item__content {
            width: 65%;
        }
    }
</style>

<style lang="less" scoped>
    .form-flex {
        text-align: left;

        .margin-left {
            margin-left: 100px;
        }
    }
</style>



