<template>
    <div ref="mychart" class="echartDiv"/>
</template>

<script>
    import echarts from 'echarts'
    import * as TreeData from '../../assets/demoData/FamilyTree/FamilyTreeData'

    export default {
        name: "FamilyTreeDetail",
        data() {
            return {
                // myChart实例
                myChart: {},
                echartsOption: {
                    title:{
                        text:'爱人信息'
                    },
                    tooltip: {
                        trigger: 'item',
                        triggerOn: 'mousemove',
                        formatter: function (params, ticket) {
                            // console.log(params.data.description)
                            let desc = params.name
                            return desc;
                        }
                    },
                    series: [
                        {
                            type: 'tree',
                            data: TreeData.mother,
                            left: '2%',
                            right: '2%',
                            top: '10%',
                            bottom: '10%',
                            symbol: 'emptyCircle',
                            symbolSize: [80, 40],
                            orient: 'vertical',
                            initialTreeDepth: 2,
                            expandAndCollapse: false,
                            label: {
                                normal: {
                                    verticalAlign: 'middle',
                                    fontSize: 18
                                }
                            },
                            leaves: {
                                label: {
                                    normal: {
                                        fontSize: 14
                                    }
                                }
                            },
                            animationDurationUpdate: 750
                        }
                    ]
                }
            }
        },
        mounted() {
            let dom = this.$refs.mychart;
            this.myChart = echarts.init(dom);
            this.myChart.setOption(this.echartsOption);
            console.log(this.$store.state.familyTree.detailID);
        }
    }
</script>

<style lang="less" scoped>
    .echartDiv {
        position: relative;
        width: 100%;
        height: 100%;
    }
</style>



