import {eventListMutations} from '../mutation-types'

const eventList = {
    namespaced: true,                    // 默认情况下，模块内部的 action、mutation 和 getter 是注册在全局命名空间的
    state: {
        eventListDay: [],                // 模块内的状态已经是嵌套的了，使用 `namespaced` 属性不会对其产生影响
        eventListWeek: [],
        eventListMonth: [],
        eventListLong: []
    },
    mutations: {
        [eventListMutations.SET_EVENTLISTDAY]: (state, payload) => {               // commit('eventList/set_eventListDay')
            state.eventListDay = payload.eventListDay
        },
        [eventListMutations.SET_EVENTLISTWEEK]: (state, payload) => {               // commit('eventList/set_eventListWeek')
            state.eventListWeek = payload.eventListWeek
        },
        [eventListMutations.SET_EVENTLISTMONTH]: (state, payload) => {               // commit('eventList/set_eventListMonth')
            state.eventListMonth = payload.eventListMonth
        },
        [eventListMutations.SET_EVENTLISTLONG]: (state, payload) => {               // commit('eventList/set_eventListLong')
            state.eventListLong = payload.eventListLong
        }
    }
};

export default eventList;



