<!--https://github.com/chenxuan0000/vue-seamless-scroll-->
<template>
    <vue-seamless-scroll :data="listData" :class-option="classOption" class="docker-scroll">
        <ul class="scroll-ul">
            <li class="scroll-li" v-for="(item,index) in listData" :key="index">
                <span class="create-time" v-text="item.date"></span>
                <span v-if="index%3 == 1" class="i-wealth">{{index}}</span>
                <span class="title" v-text="item.title"></span>
                <div class="devide-line"/>
            </li>
        </ul>
    </vue-seamless-scroll>
</template>

<script>
    export default {
        name: "ScrollItem",
        data() {
            return {
                listData: [{
                    'title': '无缝滚动第一行无缝滚动第一行',
                    'date': '2017-12-16'
                }, {
                    'title': '无缝滚动第二行无缝滚动第二行',
                    'date': '2017-12-16'
                }, {
                    'title': '无缝滚动第三行无缝滚动第三行',
                    'date': '2017-12-16'
                }, {
                    'title': '无缝滚动第四行无缝滚动第四行',
                    'date': '2017-12-16'
                }, {
                    'title': '无缝滚动第五行无缝滚动第五行',
                    'date': '2017-12-16'
                }, {
                    'title': '无缝滚动第六行无缝滚动第六行',
                    'date': '2017-12-16'
                }, {
                    'title': '无缝滚动第七行无缝滚动第七行',
                    'date': '2017-12-16'
                }, {
                    'title': '无缝滚动第八行无缝滚动第八行',
                    'date': '2017-12-16'
                }, {
                    'title': '无缝滚动第九行无缝滚动第九行',
                    'date': '2017-12-16'
                }, {
                    'title': '无缝滚动第九行无缝滚动第10行',
                    'date': '2017-12-16'
                }, {
                    'title': '无缝滚动第九行无缝滚动第11行',
                    'date': '2017-12-16'
                }, {
                    'title': '无缝滚动第九行无缝滚动第12行',
                    'date': '2017-12-16'
                }]
            }
        },
        computed: {
            classOption: function () {
                return {
                    step: 1,
                    limitMoveNum: 10,
                    singleHeight: 60,
                    waitTime: 100000
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    .docker-scroll {
        height: 100%;
        width: 100%;
        overflow: hidden;
        background: #FFFFFF;

        .scroll-ul {
            list-style: none;
            text-align: left;

            .scroll-li {
                position: relative;
                width: 100%;
                height: 60px;
                background: url("/static/img/main/module_bg.png") -434px -2975px no-repeat;
                display: table-row;

                .i-wealth {
                    width: 25px;
                    height: 16px;
                    color: #d34f1f;
                    padding-left: 17px;
                    font-size: 12px;
                    margin-top: 2px;
                    line-height: 18px;
                    background: url("/static/img/common.png") -235px -119px no-repeat;
                }

                .create-time {
                    margin-left: 20px;
                    font-size: 16px;
                    line-height: 54px;
                    color: #8b8b8b;
                }
                .title {
                    margin-left: 15px;
                    color: #2d64b6;
                }
                .devide-line {
                    width: 265px;
                    height: 1px;
                    margin-left: 115px;
                    background: url("/static/img/main/module_x.png") 0px 0px repeat-x;
                }
            }
        }
    }

</style>



