const defaultInfo = {
    id: 1,
    title: '书籍是人类进步的阶梯',
    desc: '<p>俗话说“书中自有颜如玉，书中自有黄金屋”。很多朋友都喜欢看书，养生专家认为，这是很好的保健养生法。' +
        '你知道阅读书籍会有哪些养生好处吗?接下来，专家就为你具体解读了阅读书籍的养生价值。' +
        '如果你是一个爱书之人，不妨给自己制定一个读书计划，经常看点书吧。</p>' +
        '<p>阅读书籍的好处</p>' +
        '<p>1.预防大脑衰老。就像身体上的肌肉一样，大脑也需要通过锻炼来变得更强壮和健康。研究发现，读书能让大脑保持活跃和忙碌，从而预防衰老。</p>' +
        '<p>2.减压。英国读书俱乐部委托萨塞克斯大学的“心智实验室”进行研究发现，在各种减压方式中，阅读效果最佳，6分钟内就能使压力水平降低68%，比听音乐和散步效果都好。心理学家认为，阅读时人们的思绪会集中在文字上，紧张的身心可以因此得到放松。</p>' +
        '<p>3.提高记忆力。每本书都有上亿个信息点，包括故事背景、人物名称、细节描写等，在阅读时，大脑会轻松地记住这些东西，并将其储存起来，同时也让你的记忆力变得更好。</p>' +
        '<p>4.生活方式更健康。美国一项研究发现，热爱阅读的人，作息更合理，饮食习惯更健康。一方面因为他们从书中汲取了更多健康方面的知识;另一方面，阅读让他们变得热爱生活，珍惜生命。</p>'
};
const DemoDataJson = {
    defaultInfo: defaultInfo
};

export default DemoDataJson;



