import {userMutations} from '../mutation-types'

const user = {
    namespaced: true,   // 默认情况下，模块内部的 action、mutation 和 getter 是注册在全局命名空间的
    state: {
        token: ''       // 模块内的状态已经是嵌套的了，使用 `namespaced` 属性不会对其产生影响this.$store.state.user.token
    },
    mutations: {
        [userMutations.SET_TOKEN]: (state, payload) => {               // commit('user/set_token')
            state.token = payload.token
        }
    }
}

export default user;



