<!--[回车事件](https://blog.csdn.net/lx_1024/article/details/79086979)-->
<template>
    <div class="docker-full">
        <el-container class="height-40">
            <el-main>
                <el-carousel height="200px" trigger="click" :autoplay="false" :interval="2000">
                    <el-carousel-item v-for="(item,index) in dataListLong" :key="item.id">
                        <h3 @drop='drop($event)' :data-index="index"
                            @dragover='allowDrop($event)'>{{ item.legend }}</h3>
                    </el-carousel-item>
                </el-carousel>
            </el-main>
        </el-container>
        <el-container>
            <el-main class="docker-full">
                <div v-for="(item,index) in dataListWeek" :key="item.id" class="event-item-container">
                    <div @dblclick="item.editState = true" :draggable="true" @dragstart='drag($event)'
                         :data-index="index" class="event-input-container">
                        <el-input v-model="item.legend" :disabled="!item.editState"
                                  @keyup.enter.native="item.editState = false"
                                  :class="{'hang': item.process == 'H', 'done':item.process == 'D'}"
                                  size="small" placeholder="请输入内容"/>
                    </div>
                    <el-select v-model="item.process" class="event-edit-container" v-if="item.editState"
                               placeholder="请选择" size="small">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </div>
            </el-main>
            <el-main class="docker-full">
                <div v-for="(item,index) in dataListMonth" :key="item.id" class="event-item-container">
                    <div @dblclick="item.editState = true" class="event-input-container">
                        <el-input v-model="item.legend" :disabled="!item.editState"
                                  @keyup.enter.native="item.editState = false"
                                  :class="{'hang': item.process == 'H', 'done':item.process == 'D'}"
                                  size="small" placeholder="请输入内容"/>
                    </div>
                    <el-select v-model="item.process" class="event-edit-container" v-if="item.editState"
                               placeholder="请选择" size="small">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </div>
            </el-main>
        </el-container>
    </div>
</template>

<script>
    import DemoData from '../../assets/demoData/eventList/EventLongData'

    export default {
        name: "EventListLong",
        data() {
            return {
                dataListLong: [],
                dataListWeek: [],
                dataListMonth: [],
                options: []
            }
        },
        mounted() {
            this.dataListLong = DemoData.dataListLong;
            this.dataListWeek = DemoData.dataListWeek;
            this.dataListMonth = DemoData.dataListMonth;
            this.options = DemoData.options;
        },
        methods: {
            allowDrop(ev) {
                ev.preventDefault();
            },
            drag(ev) {
                console.log(ev.target.getAttribute('data-index'));
                ev.dataTransfer.setData("data-index", ev.target.getAttribute('data-index'));
            },
            drop(ev) {
                ev.preventDefault();
                var data = ev.dataTransfer.getData("data-index");
                let myIndex = ev.target.getAttribute('data-index');
                this.dataListLong[myIndex].legend = this.dataListWeek[data].legend;
            },
            synchronousData(){
                this.$store.commit({
                    type: 'eventList/set_eventListWeek',
                    eventListWeek: this.eventListDay
                });
            }
        }
    }
</script>

<style>
    .el-input.is-disabled .el-input__inner {
        color: #000000;
        background-color: #3a8ee6;
    }

    .el-input.hang .el-input__inner {
        background-color: #e6a23c;
    }

    .el-input.done .el-input__inner {
        background-color: #67c23a;
    }
</style>

<style scoped>
    .height-40 {
        height: 40%;
    }

    .event-item-container {
        margin-top: 5px;
        text-align: left;

        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: flex-start;
        align-items: flex-start;
    }

    .event-input-container {
        flex: 1;
        padding-left: 15px;
        background: #CCCCCC;
    }

    .event-edit-container {
        width: 60px
    }
</style>



