<template>
    <div class="docker-full">
        <el-container class="height-50">
            <el-main>
                <img class="full-img" :src="davinciData.firstItem.src"
                     :title="davinciData.firstItem.title"/>
            </el-main>
            <el-main>
                {{davinciData.secondItem.desc}}
            </el-main>
        </el-container>
        <el-container class="height-50">
            <el-main>
                {{davinciData.thirdItem.desc}}
            </el-main>
            <el-main>
                <img class="full-img" :src="davinciData.fourthItem.src"
                     :title="davinciData.fourthItem.title"/>
            </el-main>
        </el-container>
    </div>
</template>

<script>
    import DemoData from '../../assets/demoData/main/DavinciDescData'

    export default {
        name: 'DavinciDesc',
        data() {
            return {
                davinciData: DemoData
            }
        }
    }
</script>

<style lang="less" scoped>
    .height-50 {
        height: 50%;
    }

    .el-main {
        padding: 0;
        width: 50%;
        text-align: left;
        text-indent:35px;
        background: yellow;

        .full-img {
            display: block;
            width: 100%;
            height: 100%;
        }
    }
</style>



