<template>
    <div class="docker-desc">
        <div class="desc-text">
            <h2>
                {{bookInfo.title}}
            </h2>
            <div class="summer" v-html="bookInfo.desc"/>
        </div>
        <div class="desc-tool">
            <a class="more">查看全文>>>{{this.$route.params.id }}</a>
            <span class="spoke">评论（99+）</span>
            <span class="reader">阅读（999+）</span>
        </div>
    </div>
</template>

<script>
    import DemoData from '../../assets/demoData/main/NavBookDescData'

    export default {
        name: "NavBookDesc",
        data() {
            return {
                bookInfo: DemoData.defaultInfo
            }
        }
    }
</script>

<style lang="less" scoped>
    .docker-desc {
        position: relative;
        width: 604px;
        height: 600px;
        background: #f4e65b;

        .desc-text {
            height: 520px;
            padding: 10px;
            overflow: hidden;

            h2 {
                padding: 10px;
                font-size: 24px;
                line-height: 30px;
                font-weight: 400;
            }
            .summer {
                text-align: left;
                line-height: 2em;
                overflow: hidden;

                /deep/ p {
                    text-indent: 2em;
                }
            }
        }
        .desc-tool {
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 60px;
            font-size: 16px;
            color: #353535;

            .more {
                position: absolute;
                left: 30px;
                top: 15px;
                color: blue;
            }
            .spoke {
                position: absolute;
                right: 145px;
                top: 15px;
            }
            .reader {
                position: absolute;
                right: 30px;
                top: 15px;
            }
        }
    }
</style>



