// 计算日期字符串几天之后的日期字符串
function addDate(dateStr, days) {
    let d = new Date(dateStr);
    d.setDate(d.getDate() + days);
    let m = d.getMonth() + 1;
    return d.getFullYear() + '-' + m + '-' + d.getDate();
}

// 计算日期字符串，增加或减少几天之后的日期字符串
function dateOperator(dateStr, days, operator) {
    dateStr = dateStr.replace(/-/g, "/"); //更改日期格式
    let nd = new Date(dateStr);
    nd = nd.valueOf();
    if (operator == "+") {
        nd = nd + days * 24 * 60 * 60 * 1000;
    } else if (operator == "-") {
        nd = nd - days * 24 * 60 * 60 * 1000;
    } else {
        return false;
    }

    nd = new Date(nd);
    let y = nd.getFullYear();
    let m = nd.getMonth() + 1;
    let d = nd.getDate();
    if (m <= 9) m = "0" + m;
    if (d <= 9) d = "0" + d;
    let cdate = y + "-" + m + "-" + d;
    return cdate;
}

/**
 * get timestamp
 * @param format yyyymmdd
 * @returns {string}
 */
function getTimestamp(format, importDate) {
    /*
        //第一种方法   1498627266000
        let timestamp1 = Date.parse(new Date());
        console.log(timestamp1);
        //第二种方法   1498627266558
        let timestamp2 = (new Date()).valueOf();
        console.log(timestamp2);
        //第三种方法   1498627266558
        let timestamp3 = new Date().getTime();
        console.log(timestamp3);
    */

    let myDate;
    if (importDate instanceof Date) {
        myDate = importDate;
    } else {
        myDate = new Date();
    }
    let fullYear = myDate.getFullYear(); //获取完整的年份(4位,1970-????)
    let month = myDate.getMonth() + 1; //获取当前月份(0-11,0代表1月)
    let date = myDate.getDate(); //获取当前日(1-31)
    let day = myDate.getDay(); //获取当前星期X(0-6,0代表星期天)
    let time = myDate.getTime(); //获取当前时间(从1970.1.1开始的毫秒数)
    let hours = myDate.getHours(); //获取当前小时数(0-23)
    let minutes = myDate.getMinutes(); //获取当前分钟数(0-59)
    let seconds = myDate.getSeconds(); //获取当前秒数(0-59)
    let milliseconds = myDate.getMilliseconds(); //获取当前毫秒数(0-999)
    let localDateStr = myDate.toLocaleDateString(); //获取当前日期 2018/9/12
    let localTimeStr = myDate.toLocaleTimeString(); //获取当前时间 下午5:34:06
    let localStr = myDate.toLocaleString(); //获取日期与时间    2018/9/12 下午5:34:06

    //格式处理
    if (month <= 9) month = "0" + month;
    if (date <= 9) date = "0" + date;
    if (hours <= 9) hours = "0" + hours;
    if (seconds <= 9) seconds = "0" + seconds;
    if (milliseconds <= 9) {
        milliseconds = "00" + milliseconds;
    } else if (milliseconds <= 99) {
        milliseconds = "0" + milliseconds;
    }
    let timestamp;
    if (format == "yyyymmdd") {
        timestamp = '' + fullYear + month + date;
    } else if (format == "年月日") {
        timestamp = '' + fullYear + '年' + month + '月' + date + '日';
    } else {
        timestamp = '' + fullYear + month + date + hours + seconds + milliseconds;
    }
    return timestamp;
}

/**
 * 获取当前时间为本年第几周
 * @param importDate Date()类型
 * @returns {number}
 */
function getWeekOfYear(importDate) {
    let today;
    if (importDate instanceof Date) {
        today = importDate;
    } else {
        today = new Date();
    }
    let firstDay = new Date(today.getFullYear(), 0, 1);
    let dayOfWeek = firstDay.getDay();
    let spendDay = 1;
    if (dayOfWeek != 0) {
        spendDay = 7 - dayOfWeek + 1;
    }
    firstDay = new Date(today.getFullYear(), 0, 1 + spendDay);
    let d = Math.ceil((today.valueOf() - firstDay.valueOf()) / 86400000);
    let result = Math.ceil(d / 7);
    return result + 1;
};

export {addDate, dateOperator, getTimestamp, getWeekOfYear}



