<template>
    <div ref="mychart" class="echartDiv"/>
</template>

<script>
    import echarts from 'echarts'
    import * as TreeData from '../../assets/demoData/FamilyTree/FamilyTreeData'
    import * as TreeDataUtil from "../../assets/demoData/FamilyTree/FamilyTreeDataUtil"
    import apis from '../../apis/FamilyTree'

    export default {
        name: "FamilyTreeBase",
        data() {
            return {
                // myChart实例
                myChart: {},
                treeData: TreeData.father
            }
        },
        methods: {
            freshTree() {
                let dom = this.$refs.mychart;
                this.myChart = echarts.init(dom);
                this.myChart.showLoading();
                let echartsOption = {
                    title:{
                        text:'基本信息'
                    },
                    tooltip: {
                        trigger: 'item',
                        triggerOn: 'mousemove',
                        formatter: function (params, ticket) {
                            // console.log(params.data.description)
                            let desc = params.name
                            return desc;
                        }
                    },
                    series: [
                        {
                            type: 'tree',
                            data: this.treeData,
                            left: '2%',
                            right: '2%',
                            top: '10%',
                            bottom: '10%',
                            symbol: 'emptyCircle',
                            symbolSize: [80, 40],
                            orient: 'vertical',
                            initialTreeDepth: 2,
                            expandAndCollapse: true,
                            label: {
                                normal: {
                                    verticalAlign: 'middle',
                                    fontSize: 18
                                }
                            },
                            leaves: {
                                label: {
                                    normal: {
                                        fontSize: 14
                                    }
                                }
                            },
                            animationDurationUpdate: 750
                        }
                    ]
                };
                this.myChart.setOption(echartsOption);
                this.myChart.hideLoading();

                let buttonList = TreeDataUtil.parseTreeData(this.treeData);
                this.$store.commit({
                    type: 'familyTree/set_buttonList',
                    buttonList: buttonList
                });
            }
        },
        mounted() {
            if (sessionStorage.getItem("isLogin")) {
                let baseId = this.$store.state.familyTree.baseID;
                console.log(baseId);
                let sendData = {
                    type: 'base',
                    id: baseId
                };
                //调用api接口
                apis.getData(sendData).then(res => {
                    let result = res.data.result;
                    // console.log(result);
                    if (result) {
                        this.treeData = result;
                        this.freshTree();
                    } else {
                        this.$message.error(result);
                    }
                }).catch(error => {
                    console.log(error);
                    this.$message.error('Operate Error')
                })
            } else {
                this.freshTree();
            }
            this.myChart.on('click', function (params) {
                console.log(params.data.id);
            });
        }
    }
</script>

<style lang="less" scoped>
    .echartDiv {
        position: relative;
        width: 100%;
        height: 100%;
    }
</style>



