<template>
    <div class="docker-full">
        <el-date-picker
            v-model="pickedDate"
            align="right"
            type="date"
            placeholder="选择日期"
            :picker-options="pickerOptions"
            @change="freshData"
            size="small"
        >
        </el-date-picker>
        <div v-for="(item, index) in eventListDay" :key="item.id" class="event-docker">
            <el-dropdown @command="handleCommand" size="small">
                <el-button type="primary" :class="{'hang': item.process == 'H', 'done':item.process == 'D'}"
                           size="small">
                    {{item.process}}
                    <i class="el-icon-arrow-down el-icon--right"/>
                </el-button>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item :command="index + '-D'">D</el-dropdown-item>
                    <el-dropdown-item :command="index + '-H'">H</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
            <el-input v-model="item.legend" placeholder="请输入内容" :disabled="!item.editState" size="small"/>
            <el-button type="primary" icon="el-icon-edit" circle @click="item.editState = true" size="small"/>
            <el-button type="success" icon="el-icon-check" circle @click="item.editState = false" size="small"
                       v-if="item.editState"/>
        </div>
        <el-row>
            <el-button type="primary" @click="addEvent" size="small" class="margin-top-20">AddEvent</el-button>
            <el-button type="success" @click="synchronousData" size="small" class="margin-top-20">Synchronous
            </el-button>
        </el-row>
    </div>
</template>

<script>
    import * as DateUtil from '../../assets/js/DateUtil.js'
    import apis from '../../apis/EventList'
    import ElData from '../../assets/demoData/ElData'
    import IndexDB from '../../assets/js/DbUtil.js'

    export default {
        name: "EventListDay",
        data() {
            return {
                defaultData: [{
                    id: DateUtil.getTimestamp(),
                    process: 'I',
                    legend: '成为地球上最能吃的松树',
                    editState: false
                }],
                eventListDay: [],
                pickerOptions: ElData.pickerOptions,
                pickedDate: ''
            }
        },
        watch: {
            eventListDay: function (val, oldVal) {
            }
        },
        mounted() {
            this.freshData();
        },
        methods: {
            handleCommand: function (command) {
                console.log(command);
                let dataArray = command.split("-");
                let index = dataArray[0];
                let process = dataArray[1];
                this.eventListDay[index].process = process;
            },
            addEvent() {
                if (this.eventListDay.length > 9) {
                    return;
                }
                let addEventItem = {
                    id: DateUtil.getTimestamp(),
                    process: 'I',
                    legend: '成为地球上最能吃的松树',
                    editState: true
                };
                this.eventListDay.push(addEventItem)
            },
            synchronousData() {
                if (sessionStorage.getItem("isLogin")) {
                    console.log('login in')
                    //日数据
                    let sendDataDay = {
                        type: 'DAY',
                        tag: DateUtil.getTimestamp('yyyymmdd', this.pickedDate),
                        data: this.eventListDay
                    };
                    //周数据
                    let sendDataWeek = {
                        type: 'WEEK',
                        tag: DateUtil.getWeekOfYear(this.pickedDate),
                        data: this.$store.state.eventList.eventListWeek
                    };

                    let sendData = {
                        sendDataDay: sendDataDay,
                        sendDataWeek: sendDataWeek
                    };
                    //调用api接口
                    apis.updateData(sendData).then(res => {
                        let result = res.data.result;
                        console.log(result);
                        if (result == 'success') {
                            this.$message.success(result);
                        } else {
                            this.$message.error(result);
                        }
                    }).catch(error => {
                        console.log(error);
                        this.$message.error('Operate Error')
                    });
                } else {
                    //未登录
                    console.log('not login')
                    let keyDay = DateUtil.getTimestamp('yyyymmdd', this.pickedDate);         //存数据的key值
                    let dataDay = this.eventListDay;                          //将被存的数据，也可以字符串

                    //同步日数据
                    IndexDB.save('kaolaDB', 'event_list', keyDay, dataDay, function (result) {
                        if (result.code == 'success') {
                            console.log(result.code)
                        } else {
                            console.error(result)
                        }
                    });

                    //同步周数据
                    let keyWeek = DateUtil.getWeekOfYear(this.pickedDate);
                    let dataWeek = this.$store.state.eventList.eventListWeek;
                    IndexDB.save('kaolaDB', 'event_list', keyWeek, dataWeek, function (result) {
                        if (result.code == 'success') {
                            console.log(result.code)
                        } else {
                            console.error(result)
                        }
                    });
                }
            },
            freshData() {
                let keyDay = this.pickedDate;
                if (!keyDay) {
                    keyDay = DateUtil.getTimestamp('yyyymmdd', null);
                } else {
                    keyDay = DateUtil.getTimestamp('yyyymmdd', keyDay);
                }
                const that = this;
                if (sessionStorage.getItem("isLogin")) {
                    let sendData = {
                        keyDay: keyDay
                    };
                    //调用api接口
                    apis.getData(sendData).then(res => {
                        let result = res.data.result;
                        console.log(result);
                        if (result && result.length > 0) {
                            that.eventListDay = result;
                        } else {
                            this.eventListDay = this.defaultData;
                        }
                    }).catch(error => {
                        console.log(error);
                    });
                } else {
                    // IndexDB.init(function (result) {
                    //     console.log(result);
                    // }, 'kaolaDB');
                    IndexDB.get('kaolaDB', 'event_list', keyDay, function (result) {
                        if (result.code == 'success') {
                            if (result.data) {
                                that.eventListDay = result.data;
                            } else {
                                that.eventListDay = that.defaultData;
                            }
                            console.log(result);
                        } else {
                            console.error(result);
                        }
                    });
                }
            }
        }
    }
</script>

<style>
    .el-input.is-disabled .el-input__inner {
        color: #000000;
    }
</style>

<style scoped>
    .event-docker {
        text-align: left;
        margin-left: 10px;
        margin-top: 15px;
    }

    .el-input {
        width: 70%;
    }

    .hang {
        background-color: #e6a23c;
    }

    .done {
        background-color: #67c23a;
    }

    .el-button {
        margin-left: 0;
    }

    .margin-top-20 {
        margin-top: 20px;
    }
</style>



