<template>
    <div class="docker-full">
        <component v-bind:is="currentTabComponent"></component>
    </div>
</template>

<script>
    import FourGrid from './DavinciDesc'

    export default {
        name: "NavPicDesc",
        components: {
            FourGrid
        },
        data() {
            return {
                descType: 'FourGrid'
            }
        },
        computed: {
            currentTabComponent() {
                return this.descType;
            }
        }
    }
</script>

<style scoped>

</style>



