<template>
    <el-container>
        <el-main>
            <div class="main-part">
                <div class="left-item">
                    <event-list-day/>
                </div>
                <div class="right-item">
                    <event-list-long/>
                </div>
            </div>
        </el-main>
    </el-container>
</template>

<script>
    import EventListDay from '../components/eventList/EventListDay'
    import EventListLong from '../components/eventList/EventListLong'

    export default {
        name: "EventList",
        components: {
            EventListDay,
            EventListLong
        }
    }
</script>

<style scoped>
    .el-main {
        text-align: center;
    }

    .main-part {
        position: relative;
        width: 100%;
        height: 600px;
        overflow: hidden;

        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: flex-start;
    }

    .main-part .left-item {
        position: relative;
        width: 560px;
        height: 100%;

        background: darkkhaki;
    }

    .main-part .right-item {
        flex: 1;
        height: 100%;
        margin-left: 1%;

        background: #ffa7b8;
    }

    .event-box {
        height: 50%;
    }
    .padding-0{
        padding: 0;
    }
</style>



