import {familyTreeMutations} from '../mutation-types'

const familyTree = {
    namespaced: true,                    // 默认情况下，模块内部的 action、mutation 和 getter 是注册在全局命名空间的
    state: {
        baseID: 7,                // 模块内的状态已经是嵌套的了，使用 `namespaced` 属性不会对其产生影响
        detailID: 8,
        buttonList: [{
            id: '',
            taId: '',
            name: '',
            parentId: '',
            description: ''
        }],
        editIndex: 0
    },
    mutations: {
        [familyTreeMutations.SET_BASEID]: (state, payload) => {
            state.baseID = payload.baseID;
            state.detailID = payload.detailID;
            state.editIndex = payload.editIndex;
        },
        [familyTreeMutations.SET_BUTTONLIST]: (state, payload) => {
            state.buttonList = payload.buttonList
        }
    }
};

export default familyTree;



